<template>
    <div class="Blog">
       <article @click="toDetails(url)">
            <h4 class="title">
                {{title}}
            </h4>
            <p class="time">
                {{time}}
                <el-tag v-for="(item, index) in tags"
                        :type="item.type"
                        size="mini"
                        :key="index">{{item.name}}</el-tag>
                {{pulishTime}}
            </p>
            <p v-html="returnBrief(brief)"
               class="brief">
                {{brief}}
            </p>
        </article>
    </div>
</template>
<script>
export default {
  name: 'Blog',
  props: {
    title: {type: String, default: () => ''},
    url: {type: String, default: () => ''},
    time: {type: String, default: () => ''},
    tags: {type: Array, default: () => []},
    brief: {type: String, default: () => ''}
  },
  methods: {
    // 跳转至详情
    toDetails (path) {
      this.$router.push('/blog/blog_' + path)
    },
    // 过滤标签后固定显示最多 textNum 个字的简介
    returnBrief (val) {
      const textNum = 70
      const _val = val.replace(/<[^>]+>/g, '')
      return _val.substring(0, textNum) + (_val.length <= textNum ? '' : '...')
    }
  },
  computed: {
    // 发布至今的时间
    pulishTime () {
      let now = new Date()
      let [year, month, date, hour, minute, second] = [this.time.substring(0, 4), this.time.substring(5, 7), this.time.substring(8, 10), this.time.substring(11, 13), this.time.substring(14, 16), this.time.substring(17, 19)]
      let before = new Date(year, month - 1, date, hour, minute, second)
      let days = (now - before) / 1000 / 60 / 60 / 24

      const switchTime = (days) => {
        switch (true) {
          case days >= 365:
            return ((days / 365) >> 0) + '年前'
          case days > 31:
            return ((days / 30) >> 0) + '个月前'
          case days < 2 && days > 1:
            return '昨天'
          case days <= 1:
            return ((days * 24) >> 0) + '小时前'
          default:
            return (days >> 0) + '天前'
        }
      }

      return switchTime(days)
    }
  }
}
</script>
<style lang="scss">
    @import './../assets/css/conifg/basic.config';

    .Blog {

        .el-tag {
            margin:0 2px;
        }

        article {

            & {
                cursor: pointer;
                padding: 0 4px;
                height: 106px;
                overflow: hidden;
                font-size: 12px;
                @include transition;
            }

            p {
                margin: 5px 0;
                font-size: 1em;
            }

            .title {
                border-left: 8px solid $primary-color;
                margin: 6px 0;
                padding-left: 4px;
                color: $dark-silver;
                font-size: 1.2em;
            }

            .time {
                padding-left: 10px;
                color: $light-silver;
                font-size: 0.9em;
            }

            .brief {
                padding: 0 10px;
                color: $dark-gray;
                font-size: 1em;
            }
        }
    }

    @media screen and ( min-width: 750px ) {
        .Blog article:hover {
            font-size: 14px;
        }
    }
</style>



